<template>
  <div>
    <!--시스템 알림 팝업-->
    <div class="chatPops">
      <div class="popTxts">
        <p v-if="isExitType">{{$t('popup.종료할까요')}}</p>
        <p v-if="isLogoutType">{{$t('popup.로그아웃')}}</p>
        <p class="popType01" v-if="isSystemCheck">{{$t('popup.시스템점검')}}</p>
        <p class="popType01 custom-error-message" v-if="isSystemError" v-html="errorMessage"></p>
      </div>
      <div class="popBtns">
        <button type="button" class="btnG" @click="$uiHandler.hideClosePopup" v-if="isExitType">{{$t('popup.취소')}}</button>
        <button type="button" class="btnB" v-if="isExitType" @click="onExit">{{$t('popup.확인')}}</button>
        <button type="button" class="btnG" v-if="isLogoutType" @click="onLogin">{{$t('popup.로그인')}}</button>
        <button type="button" class="btnB" v-if="isLogoutType" @click="onExit">{{$t('popup.챗봇종료')}}</button>
        <button type="button" class="btnB btnLg" v-if="isSystemErrorType" @click="onSystemError">{{$t('popup.확인')}}</button>
      </div>
    </div>
    <!--//시스템 알림 팝업-->
    <div class="dimmed" id="system-alarm-dimmed"></div>
  </div>
</template>

<script>
export default {
  name: 'SystemPopup',
  watch: {
    type() {
      if (!this.isSystemError) {
        return;
      }

      this.$uiHandler.showClosePopup();
    },
  },
  computed: {
    errorMessage() {
      return this.$store.state.system.errorMessage;
    },
    type() {
      return this.$store.state.system.type;
    },
    isExitType() {
      return this.type === this.$dictionary.systemPopupType.EXIT;
    },
    isLogoutType() {
      return this.type === this.$dictionary.systemPopupType.LOGOUT;
    },
    isSystemCheck() {
      return this.type === this.$dictionary.systemPopupType.SYSTEM_CHECK;
    },
    isSystemError() {
      return this.type === this.$dictionary.systemPopupType.SYSTEM_ERROR;
    },
    isSystemErrorType() {
      const isNotError = [
        this.$dictionary.systemPopupType.EXIT,
        this.$dictionary.systemPopupType.LOGOUT,
      ].includes(this.type);
      return !isNotError;
    },
  },
  methods: {
    close() {
      this.$uiHandler.hideClosePopup();
      this.$store.dispatch('RESET_CALL_SYSTEM_ERROR');
    },
    onExit() {
      // window.close();
      window.cbClose();
    },
    onSystemError() {
      this.close();
    },
    onLogin() {
      window.location = this.$store.state.url_home;
    },
  },
};
</script>
<style>
.popType01.custom-error-message {
  font-weight: normal;
}
</style>